body {
    margin: 0;
    padding: 0;
    overflow: hidden;

    display: flex;
    height: 100vh;

    align-items: center;
    justify-content: center;
  }


.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-size: 110%;
  cursor: none;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 247, 253);
  /* box-shadow: 0 40px 80px #FBE4E0; */
  border-radius: 14px;
  padding: 50px;
  box-sizing: border-box;
  margin: 0;
  z-index: 1;
  cursor: none;
}

label {
  padding-bottom: 4rem;
  cursor: none;
}

input[type=password] {
  margin: 10px;
  padding: 10px;
  font-size: 20px;
  border-radius: 5px rgb(255, 231, 249);
  outline: none;
  text-align: center;
  cursor: none;
}

