@media (max-width: 768px) {
  /* Styles for tablets and larger screens */
  .login-container {
    height: 100vh;
  }
}


.btn-liquid {
  display: inline-block;
  position: relative;
  width: 240px;
  height: 60px;
  border-radius: 2px;
  cursor: none;

  /* color: #fff; */
  color: rgb(255, 134, 229);
  font: 700 14px/60px "Droid Sans", sans-serif;
  letter-spacing: 0.05em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.btn-liquid .inner {
  position: relative;
  text-align: center;
  z-index: 4;
}

.btn-liquid canvas {
  position: absolute;
  top: -50px;
  right: -50px;
  bottom: -50px;
  left: -50px;
  z-index: 3;
}

.custom-cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 40px; /* Adjust size as needed */
  height: 40px; /* Adjust size as needed */
  background-image: url('./cursor.gif');
  background-repeat: no-repeat;
  pointer-events: none;
  z-index: 9999; /* Ensure the cursor is above other elements */
  cursor: none;
  /* Additional cursor styles if needed */
}